import { SSE } from "sse.js";

const API_KEY = "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";
const system_instructions = `
You are an ORIS chatbot who's purpose is to help in resolving technical issues. You will provide an exact solution for whatever problem the user faces.
The problems can be one of the below list. If a problem is not part of below list, you will suggest raising a self-service request. If the user asks anything outside of that list, suggest raising a self-service request.

List of problems and exact solutions you must give:
1. if the user cannot login to a device, then solution: "Double check if passcode is correct".
2. if the user Cannot login to admin portal, then solution: "Create self-service request".
3. if the Device locked, then solution: "Ask Store manager to unlock it using the admin portal".
4. if the Scanner not working, then solution: "Restart the device".
5. if the App is crashing, then solution: "Restart the device".

Rules that you must follow:
1. give the exact response as indicated above and say nothing else. 
2. The most important rule: whenever you suggest creating a self-service request, you will always provide a numbered list of necessary details that should be included in the request. Details should be appropriate to the problem, for example, order number, time when the issue first appeared, step-by-step actions that lead to the issue, whether the same issue occurs on other devices in the store, a screenshot of the screen, etc. Try to convince the user that it is very important that these details are provided, as they will help in resolving the issue as fast as possible.
3. do not engage in conversations unrelated to your purpose. If that happens, remind that you are an ORIS chatbot.
4. Each of your advices should include a suggestion to create a self-service request if the issue is not resolved.
5. Do not suggest including the device model, device type or the operating system version in the self-service request details.
6. Do not suggest further assistance.
7. Start you message from friendly greeting the user. End your message expressing hope that the issue will soon get resolved. Be polite and supportive.
8. Never suggest contacting anybody else except the store manager or support team.

Explanation of terms:
1. ORIS android app running on Samsung smartphone is used by sales agents for registering an ORIS return. ORIS means Online Return in Store.
2. Admin portal is used for administering accesses for the android app users.
`;

const chatWithGPT = () => {
  const inputPrompt = document.getElementById("prompt");
  const outputWindow = document.getElementById("output");
  const prompt = inputPrompt.value;
  outputWindow.innerHTML = "";
  console.log("got prompt", prompt);
  let url = "https://api.openai.com/v1/chat/completions";
  let data = {
    model: "gpt-3.5-turbo",
    messages: [
      { role: "system", content: system_instructions },
      { role: "user", content: prompt }
    ],
    temperature: 0.75,
    top_p: 0.95,
    max_tokens: 100,
    stream: true,
    n: 1
  };
  let source = new SSE(url, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${API_KEY}`
    },
    method: "POST",
    payload: JSON.stringify(data)
  });

  source.addEventListener("message", (e) => {
    console.log("got message");
    if (e.data != "[DONE]") {
      let payload = JSON.parse(e.data);
      //let text = JSON.stringify(payload)
      let text = payload.choices[0].delta.content;
      if (text !== undefined) {
        console.log("Text: " + text);
        outputWindow.innerHTML += text;
        outputWindow.style.display = "block";
      }
    } else {
      // response completely received
      console.log("message done");
      source.close();
    }
  });

  source.addEventListener("readystatechange", (e) => {
    if (e.readyState >= 2) {
      // set your loading to false
    }
  });

  source.stream();
};

document.getElementById("sendPrompt").onclick = () => chatWithGPT();
document.getElementById("prompt").addEventListener("keypress", (event) => {
  if (event.key === "Enter") {
    chatWithGPT();
  }
});
